import './index.scss';
import { useTranslation } from 'react-i18next';
import cardImg1 from '../../../assets/images/benefits-card-1.png';
import cardImg2 from '../../../assets/images/benefits-card-2.png';
import cardImg3 from '../../../assets/images/benefits-card-3.png';
import cardImg4 from '../../../assets/images/benefits-card-4.png';

function ProductsBenefits({ bgColor = '#FAFAFA' }) {
  const { t } = useTranslation();

  return (
    <div className="products__benefits" style={{backgroundColor: bgColor}}>
      <div className="container">
        <h3 className="pn-semibold">{ t('products.benefits') }</h3>
        <div className="products__benefits-cards d-flex">
          <div className="products__benefits-card">
            <img src={cardImg1} alt="" />
            <p>{ t('products.benefitsCardT1') }</p>
          </div>
          <div className="products__benefits-card">
            <img src={cardImg2} alt="" />
            <p>{ t('products.benefitsCardT2') }</p>
          </div>
          <div className="products__benefits-card">
            <img src={cardImg3} alt="" />
            <p>{ t('products.benefitsCardT3') }</p>
          </div>
          <div className="products__benefits-card">
            <img src={cardImg4} alt="" />
            <p>{ t('products.benefitsCardT4') }</p>
          </div>
        </div>
      </div>
    </div>
  )
}

export default ProductsBenefits;