import './index.scss';
import { useTranslation } from 'react-i18next';
import img from '../../../../assets/images/details-2.png';

function ProductDetails2() {
  const { t } = useTranslation();

  return (
    <div className="product__details2">
      <div className="container">
        <div className="product__details2__inner d-flex">
          <div className="product__details2__left">
            <h2 className="pn-bold">{ t('products.detailsT') }</h2>
            <p className="text-md">{ t('products.detailsD') }</p>
          </div>
          <div className="product__details2__right">
            <img src={img} alt="" />
          </div>
        </div>
      </div>
    </div>
  )
}

export default ProductDetails2;