import './index.scss';
import { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import ProductsCard from '../../ProductsCard';
import arrowRight from '../../../assets/images/arrow-right.svg';

function MainTopProducts() {
  const { t } = useTranslation();

  const [cards, setCards] = useState([]);

  async function fetchCards() {
    const response = await fetch('https://pga.uz/api/products');
    const data = await response.json();

    return data;
  }

  useEffect(async () => {
    setCards(await fetchCards());
  }, [])

  return (
    <div className="products__cards">
      <div className="container">
        <div className="products__cards__inner d-flex">
          {
            cards.map(card => {
              return (
                <div
                  className="products__cards-item"
                  key={card.id}
                >
                  <Link to="/pga/products/tube">
                    <ProductsCard card={card} />
                  </Link>
                </div>
              )
            })
          }
        </div>
      </div>
    </div>
  )
}

export default MainTopProducts;