import './index.scss';
import { useTranslation } from 'react-i18next';
import img from '../../../../assets/images/products-description.png';

function ProductDescription() {
  const { t } = useTranslation();

  return (
    <div className="product__description">
      <div className="product__description__inner">
        <div className="product__description__left">
          <div className="container">
            <div className="pr-2">
              <h1 className="pn-bold">{ t('products.product1T') }</h1>
              <p className="text-md">{ t('products.product1D') }</p>
            </div>
          </div>
        </div>
        <div className="product__description__right d-flex align-center">
          <img src={img} alt="" />
        </div>
      </div>
    </div>
  )
}

export default ProductDescription;